.footer {
    background-color:rgb(77,166,174);
    color: rgba(250, 216, 144, 0.635);
    text-align: center;
    padding: 1rem;
}

.social-list {
    display: flex;
    justify-content: center;
    list-style-type: none;
}

.social-list li {
    margin: 0 1rem;
}

.social-list li:hover {
    color: rgb(255,111,63);
}

.social-list svg {
    font-size: 1.2rem;
    cursor: pointer;
}

.copy_right {
    margin-top: 2rem;
}